<template>
    <div>
    <div class="user-card">
        <div class="user-card-head">

        </div>

        <div class="user-card-body">
            <div class="user-card-body-left">
                <img :src="user.photo" class="img-fluid">
            </div>
            <div class="user-card-body-right">
                <div class="user-card-body-right-text">
                    <div class="user-card-body-right-text-username">
                        <!-- {{ $store.state.user.username}} -->
                        {{ user.username }}
                        <span style="font-size: 12px; color:orange; font-style: italic;">Lv 999</span>
                    </div>
                    <div class="user-card-body-right-text-reputation">
                        <span class="user-card-body-right-text-reputation-item">
                            <span>58</span>
                        <span>关注</span>
                        </span>

                        <span class="user-card-body-right-text-reputation-item">
                            <span>{{ user.followerCount }}</span>
                        <span>粉丝</span>
                        </span>

                        <span class="user-card-body-right-text-reputation-item">
                            <span>{{ user.praise }}</span>
                        <span>赞</span>
                        </span>
                    </div>
                </div>
                <div class="user-card-body-right-button">
                    <a>
                        <button @click="follow"    v-if="!user.is_follow">+关注</button>
                        <button @click="unfollow"  v-if="user.is_follow">取消关注</button>
                        <button v-if="is_me" @click="post_a_post">发帖</button>
                    </a>
                </div>
            </div>
        </div>
    </div>
    <div class="form-floating">
            <label for="floatingTextarea2"></label>
            <textarea v-model="content" class="form-control"  id="floatingTextarea2" style="height: 50px;"></textarea>
    </div>
</div>
</template>

<script>
import { ref } from 'vue';
import $ from 'jquery'
import { useStore } from 'vuex';
import { computed } from 'vue';

export default {
    name: "UserProfileInfo",
    props:{
        user:{
            type:Object,
            required:true,
        }
    },

    setup(props,context){
        let content=ref('');
        let store=useStore();

        let is_me=computed(()=>store.state.user.id === props.user.id);

        const follow=()=>{
            context.emit('follow');
        }

        const unfollow=()=>{
            context.emit('unfollow');
        }

        const post_a_post=()=>{
            if(content.value=='') return;
            context.emit('post_a_post',content.value);

            $.ajax({
                type: "POST",
                url: "https://app165.acapp.acwing.com.cn/myspace/post/",
                headers:{
                    'Authorization':"Bearer "+store.state.user.access,
                },
                data: {
                    content:content.value,
                },
            });
             content.value='';
        }

        return{
            follow,
            unfollow,
            content,
            post_a_post,
            is_me,
        }
    }
}
</script>

<style scoped>
 .user-card {
    width: 366px;
    height: 215px;
    border-radius: 5px;
    box-shadow: 2px 2px 5px rgb(248, 248, 248);
}

.user-card-head {
    height: 85px;
    background-size: cover;
    width: 100%;
    background-image: url("https://cdn.acwing.com/media/article/image/2022/03/01/1_3ec4c1cf99-mountain.jpg");
}

.user-card-body {
    width: 100%;
    height: 140px;
    box-sizing: border-box;
    padding-top: 12px;
}

.user-card-body-left {
    width: 70px;
    height: 100%;
    float: left;
    text-align: center;
}

.user-card-body-right {
    width: 296px;
    height: 100%;
    float: left;
}

.user-card-body-left img {
    width: 48;
    height: 48;
    border-radius: 50%;
}

.user-card-body-right-text {
    width: 100%;
    height: 70%;
}

.user-card-body-right-text-username {
    font-size: 16px;
    color: #222222;
    padding: 10px;
}

.user-card-body-right-text-reputation-item {
    margin: 20px;
}

.user-card-body-right-text-reputation-item span:nth-child(1) {
    font-size: 12px;
    color: #222222;
}

.user-card-body-right-text-reputation-item span:nth-child(2) {
    font-size: 12px;
    color: #242b35;
}

.user-card-body-right-text-reputation-person {
    font-size: 13px;
    padding-top: 10px;
    padding-left: 20px;
}

.user-card-body-right-button button:nth-child(1) {
    width: 100px;
    height: 30px;
    background-color: #00AEEC;
    border: none;
    margin: 0 8px 0 0;
    color: white;
    cursor: pointer;
}

.user-card-body-right-button button:nth-child(2) {
    width: 102px;
    height: 30px;
    color: #6D757A;
    border: #6D757A solid 1px;
    cursor: pointer;
}

.user-card-body-right-button button:nth-child(2):hover {
    background-color: #00B5E5;
}

.form-floating{
    margin-top:20px;
}
</style>
