<template>
    <ContentBase>
       <div class="row">
        <div class="col-4">
            <UserProfileInfo @post_a_post="post_a_post" @follow='follow' @unfollow='unfollow' :user="user" />

        </div>
        
        <div class="col-8">
            <UserProfilePosts  :user="user" :posts="posts" @delete_a_posts="delete_a_posts" />
        </div>
       </div>
    </ContentBase>
</template>
   
<script>
import ContentBase from '../components/ContentBase';
import UserProfileInfo from '../components/UserProfileInfo';
import UserProfilePosts from '@/components/UserProfilePosts.vue';
import { useRoute } from 'vue-router';
import { reactive } from 'vue';
import $ from 'jquery'
import { useStore } from 'vuex';
export default {
    name: "UserProfileView",
    components: {
        ContentBase,
        UserProfileInfo,
        UserProfilePosts,
    },
    setup(){
        const route=useRoute();
        const userId=route.params.userId;
        const store=useStore();
        // console.log(userId);
        const user=reactive({
                id: "",
                username:"",
                photo:"",
                followerCount:"",
                is_follow:"",
        })
        const posts=reactive({
        })
        $.ajax({
            type: "GET",
            url: "https://app165.acapp.acwing.com.cn/myspace/getinfo/",
            data: {
                user_id:userId,
            },
            headers:{
                'Authorization':"Bearer "+ store.state.user.access,
            },
            success(resp) {
                user.id=resp.id;
                user.username=resp.username;
                user.photo=resp.photo;
                user.followerCount=resp.followerCount;
                user.is_follow=resp.is_follow;
            }
        });

        $.ajax({
            type: "GET",
            url: "https://app165.acapp.acwing.com.cn/myspace/post/",
            data: {
                user_id:userId,
            },
            headers:{
                'Authorization':"Bearer "+ store.state.user.access,
            },
            success (resp) {
                // console.log(resp)
                posts.posts=resp;
                posts.count=resp.length;
            }
        });

    const follow=()=>{
        if(user.is_follow) return;
        user.is_follow=true;
        user.followerCount++;
    }

    const unfollow=()=>{
        if(!user.is_follow) return;
        user.is_follow=false;
        user.followerCount--;
    }

    const post_a_post=(content)=>{
        posts.count++;
        posts.posts.unshift({
                id:posts.count,
                userId:1,
                content:content,
        })
    }
    const delete_a_posts=post_id=>{
        posts.posts=posts.posts.filter(post=> post.id!=post_id)
        posts.count=posts.posts.length;
    }

        return{
            user:user,
            follow,
            unfollow,
            posts,
            post_a_post,
            delete_a_posts,
        }
    }

}
</script>

