<template>
    <ContentBase>
       <div class="card" v-for="user in users" :key="user.id" @click="open_user_profile(user.id)">
        <div class="card-body">
            <div class="row">
                <div class="col-1">
                    <img class="img-fluid" :src="user.photo" >
                </div>
                <div class="col-11">
                    <div class="username">{{ user.username }}</div>
                    <div class="followercount">{{ user.followerCount }}</div>
                </div>
            </div>
        </div>
       </div>
    </ContentBase>
</template>
   
<script>
import ContentBase from '../components/ContentBase';
import $ from 'jquery';
import { ref } from 'vue';
import router from '@/router/index';
import { useStore } from 'vuex';
export default {
    name: "UserListView",
    components: {
        ContentBase,
    },

    setup(){

        let users=ref([]);

        $.ajax({
            type: "get",
            url: "https://app165.acapp.acwing.com.cn/myspace/userlist/",
            success(resp){
                users.value=resp;
            }
        });

        const store=useStore();
        const open_user_profile=userId=>{
            if(store.state.user.is_login){
                router.push({
                    name:"userprofile",
                    params:{
                        userId,
                    }
                })
            }
            else{
                router.push({
                    name:"login",
                })
            }
        }

        return{
            users,open_user_profile,
        }
    }

}
</script>

<style scoped>
img{
    border-radius: 50%;
}
.username{
    font-weight: bold;
    height: 50%;
}

.followercount{
    height: 50%;
    font-size: 12px;
    color: gray;
}
.card{
    margin-bottom: 20px;
    cursor: pointer;
    transition:500ms;
}
.card:hover{
    box-shadow: 2px 2px 10px lightgray;
}
</style>