<template>
    <div class="card">
        <div class="card-body">
            <div v-for="post in posts.posts" :key="post.id">
                <div class="card">
                    <div class="card-body">
                        {{ post.content }}
                        <button v-if="is_me" @click="delete_a_posts(post.id)" type="button" class="btn btn-danger  btn-secondary btn-sm" >删除</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { useStore } from 'vuex';
import { computed } from 'vue';
import $ from 'jquery';
export default {
    name:"UserProfilePosts",
    props:{
       posts:{
        type:Object,
        require:true,
       },
       user:{
        type:Object,
        require:true,
       }
    },

    setup(props,context){
        const store=useStore();
        let is_me=computed(()=>store.state.user.id === props.user.id);

        const delete_a_posts=post_id=>{
            context.emit('delete_a_posts',post_id)
            $.ajax({
                type: "DELETE",
                url: "https://app165.acapp.acwing.com.cn/myspace/post/",
                data: {
                    post_id:post_id,
                },
                headers:{
                    'Authorization':"Bearer "+store.state.user.access,
                },
            });
        }
        return{
            is_me,
            delete_a_posts,
        }
    }

}
</script>

<style scoped>
button{
    float:right
}
</style>